.childDiv{
    background-color: white;
    width: 100%;
    height: 50px;
    border-top: 0.5px solid grey;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    bottom: 0;
    z-index: 1;
    position:fixed;
}

.iconsDiv{
    width: 30px;
    height: 30px;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}